<template>
  <a-layout style="min-height: 100vh">
    <a-layout-sider v-model:collapsed="collapsed" theme="light" collapsible>
      <div class="logo" ></div>
      <a-menu :selectedKeys="selectedKeys" theme="light" mode="inline">
        <a-menu-item v-for="projectName in projects" :key="projectName">
        <router-link :to="'/merge/' + projectName">

          <pie-chart-outlined />
          <span>{{ projectName }}</span>
        </router-link>

        </a-menu-item>

      </a-menu>
    </a-layout-sider>
    <a-layout >
      <a-layout-content style="margin: 16px 16px; ">
        <div :style="{ padding: '24px', background: '#ffffff99', minHeight: '360px' }">
          <router-view></router-view>
        </div>
      </a-layout-content>
      
    </a-layout>
  </a-layout>
</template>

<script>
import projectApi from '@/apis/project'

export default {
  name: 'App',
  data:function () {
            return {
              projects: []
            }
  },
  mounted:function() {
    const _this = this
    projectApi.getProjects().then(function(data) {

      const names = []
      data.forEach(p => names.push(p.projectName))
      _this.projects = names
    })
  }
}
</script>

