import request from '@/utils/request'

export default {
    getProjects() {
        return request({
          method: 'get',
          url: '/api/projects'
        })
    },
    getProject(projectName) {
        return request({
            method: 'get',
            url: '/api/project',
            params: {
                'projectName':projectName
            }
        })
    },
    getBranchs(projectName) {
        return request({
            method: 'get',
            url: '/api/branchs',
            params: {
                'projectName':projectName
            }
        })
    }
}

